.trello_form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
}
.trello_form form{
    display: flex;
    flex-direction: column;

    justify-content: center;
    flex-wrap: wrap;
    row-gap: 10px;
    color: white;
    padding: 20px 30px 30px 30px;
    background-color: rgba(233, 247, 255, 0.68);
    box-shadow: 3px 3px 13px 3px rgba(99,99,99,0.63);
    border-radius: 10px;
}