.card_item{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: flex-start;
    row-gap: 1rem;
    background-color: rgba(233, 247, 255, 0.68);
    box-shadow: 3px 3px 13px 3px rgba(99,99,99,0.63);
    border-radius: 10px;
    padding: 20px;
}

.card_item_list {
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.card_item_form{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    row-gap: 10px;
}

.card_item_top {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}